#commonHeadNav {
    width: 100%;
    height: 30px;
    font-size: 12px;
    line-height: 30px;
    background-color: #002140;
    display: flex;
    color: #9da0a4;
    border-bottom: 1px solid hsla(0,0%,100%,.15);
}

#commonHeadNav ul {
    list-style: none;
    padding: 0px;
    margin: 0px;
}

#commonHeadNav li {
    padding: 0px;
}

#commonHeadNav a {
    text-decoration: none;
}

#commonHeadLogo {
    cursor: pointer;
    text-align: center;
    font-size: 14px;
    font-weight: 700;
    width: 180px;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
    /*background-image: url(data:image/png;base64,)*/
}

#commonHeadMenu {
    display: flex;
    flex-grow: 1;
    height: 100%;
    padding-left: 0px;
}

#commonHeadMenu > li {
    position: relative;
    text-align: center;
    margin-left: 10px;
    margin-right: 10px;
    padding: 0px;
}

#commonHeadMenu a {
    text-decoration: none;
    color: #9da0a4;
}

#commonHeadMenu a:hover {
    color: #409EFF;
}

#commonHeadSearchContainer {
    width: 160px;
    height: 100%;
    padding-top: 2px;
    padding-bottom: 2px;
    box-sizing: border-box;
}

#commonHeadSearch {
    width: 100%;
    height: 100%;
    display: flex;
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.62);
    position: relative;
    box-sizing: border-box;
}

#commonHeadSearch.active,
#commonHeadSearch:hover {
    border: 1px solid #409EFF;
}

#commonHeadSearch input {
    background: transparent;
    color: #ffffff;
    border: 0px;
    padding-left: 10px;
    width: 120px;
}

#commonHeadSearch input:focus {
    border: 0px!important;
    outline: none;
}

.common-head-search-icon {
    width: 40px;
    height: 100%;
    /*dbdbdb*/
    background-image: url(data:image/png;base64,);
}

#commonHeadUser .user-icon,
.common-head-search-icon,
.common-head-msg-icon {
    background-repeat: no-repeat;
    background-position: center;
    background-size: 14px;
}

#commonHeadMessage {
    width: 40px;
    height: 100%;
    position: relative;
}

.common-head-msg-icon {
    width: 100%;
    height: 100%;
    background-image: url(data:image/png;base64,);
}

#commonHeadUser {
    width: 100px;
    height: 100%;
    margin-right: 15px;
    position: relative;
}

#commonHeadSearch,
#commonHeadMessage,
#commonHeadUser {
    cursor: pointer;
}

#commonHeadMessage:hover,
#commonHeadUser:hover {
    background-color: #191d21;
}

.highlight-flag {
    color: red;
    margin-left: 5px;
}

#commonHeadMenu > li.active .common-head-second-nav,
#commonHeadMenu > li.active .common-head-triangle {
    display: block;
}
.common-head-second-nav {
    text-align: left;
    position: absolute;
    white-space: nowrap;
    display: none;
}

.common-head-triangle {
    width: 0;
    height: 0;
    border: 7px solid transparent;
    border-bottom-color: #fff;
    position: absolute;
    z-index: 1000;
    top: 16px;
    left: calc(50% - 7px);
    display: none;
}

.common-head-msg-count {
    min-width: 10px;
    text-align: center;
    line-height: 10px;
    display: inline-block;
    position: absolute;
    right: -2px;
    top: -1px;
    background: red;
    color: #fff;
    border-radius: 17px;
    padding: 6px;
    font-size: 10px;
    transform: scale(.7);
    font-family: Tahoma!important;
}

.common-head-msg-list {
    position: absolute;
    width: 150px;
    right: 0px;
    top: 30px;
    display: none;
}

.common-head-msg-list li {
    line-height: 18px;
    margin-top: 5px;
    margin-bottom: 5px;
    padding: 0px    ;
}

.common-head-msg-list .more {
    color: #409EFF!important;
}

.common-head-msg-list .msg-title {
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}

#commonHeadNav .panel {
    box-shadow: 0 2px 6px 0 rgba(0,0,0,.16);
    font-size: 12px;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
    z-index: 1000;
    background: #ffffff;
}

#commonHeadNav .panel a,
#commonHeadNav .panel li {
    color: rgba(0, 0, 0, 0.57);
}

#commonHeadNav .panel a:hover,
#commonHeadNav .panel li:hover {
    color: #409EFF;
}

#commonHeadMessage:hover .common-head-triangle,
#commonHeadMessage:hover .common-head-msg-list {
    display: block;
}

.common-head-search-dropdown {
    position: absolute;
    width: 100%;
    top: 30px;
    display: none;
}

.common-head-search-dropdown .search-value {
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
}

#commonHeadSearch.active .common-head-search-dropdown {
    display: block;
}

.common-head-login-btn {
    width: 100%;
    height: 100%;
    text-align: center;
}

.common-head-user-btn {
    width: 100%;
    display: none;
    position: absolute;
    right: 0px;
    top: 30px;
}

#commonHeadUser:hover .common-head-triangle,
#commonHeadUser:hover .common-head-user-btn {
    display: block;
}

.common-head-login-user {
    display: flex;
    height: 100%;
}

.common-head-login-user .user-icon {
    width: 30px;
    height: 100%;
    background-image: url(data:image/png;base64,);
}